import arr from '@/content/color.js';
function consoleColor(colorStr,text='文字描述'){
	console.log(
		"%c    %c "+colorStr+' '+text,
		`background:${colorStr};`,
		`color:${colorStr};`
	)
}

const offsetArr=arr.filter(m=>{
	return m[2]!=='colorName'&&!arr.find(checkLevel=>{
		return checkLevel[0]==(
			'#' + 
				checkLevel[0]
				.slice(1)
				.split('')
				.map(cm=>{ 
				return ( 0xf - ( '0x' + cm ) ).toString(16) }).join('')
		)
	})
}).map(m=>{
	return [
		'#'+m[0].slice(1).split('').map(im=>{ return ( 0xf - ( '0x' + im ) ).toString(16) }).join('') ,
		`${m[0]}的互补色`
	]
});
export let finalArr=[].concat(arr,offsetArr).filter(m=>m).sort((next,prev)=>{
	return -( ( '0x' + next[0].slice(1) ) - ( '0x'+prev[0].slice(1) ) )
})
finalArr.forEach(argArr=>{
	consoleColor(...argArr)
});
