
<style scoped lang='scss'>
    .colorCollection {
        padding-left: 60px;
        &>li {
            list-style:armenian;
        }
    }
</style>


<template>
    <div>
        <div class="colorCtrler">
            <label for="colorCtrler"
                :style="{
                    color:colorCtrlColor
                }"
            >当前颜色 {{ colorCtrlColor }}</label>
            <input id='colorCtrler' type="color" v-model='colorCtrlColor'>
        </div>
        <div class="hexColorInputor">
            <label for="hexColorCtrlColor"
                :style="{
                    color:'#'+hexColorCtrlColor
                }"
            >输入 3 或 6 位 十六进制颜色码</label>
            <input id='hexColorCtrlColor' type="input" v-model='hexColorCtrlColor'>
        </div>
        <div class="rgbColorInputerList">
            <div class="colorShower"
                :style="{
                    color:'rgb('+rgb.red+','+rgb.green+','+rgb.blue+')'
                }"
            >
                RGB 颜色
            </div>
            <div class="ctrlList">
                <div class="rgbCtrlColor">
                    <span>Red (0-255)</span><input type="number" v-model.number="rgb.red">
                </div>
                <div class="rgbCtrlColor">
                    <span>Green (0-255)</span><input type="number" v-model.number="rgb.green">
                </div>
                <div class="rgbCtrlColor">
                    <span>Blue (0-255)</span><input type="number" v-model.number="rgb.blue">
                </div>
            </div>
        </div>
        <div class="rgbaColorInputerList">
            <div class="colorShower"
                :style="{
                    color:'rgba('+rgba.red+','+rgba.green+','+rgba.blue+','+rgba.alpha+')'
                }"
            >
                RGBA 颜色
            </div>
            <div class="ctrlList">
                <div class="rgbaCtrlColor">
                    <span>Red (0-255)</span><input type="number" v-model.number="rgba.red">
                </div>
                <div class="rgbaCtrlColor">
                    <span>Green (0-255)</span><input type="number" v-model.number="rgba.green">
                </div>
                <div class="rgbaCtrlColor">
                    <span>Blue (0-255)</span><input type="number" v-model.number="rgba.blue">
                </div>
                <div class="rgbaCtrlColor">
                    <span>Alpha (0-1)</span><input type="number" v-model.number="rgba.alpha">
                </div>
            </div>
        </div>
        <ul class='colorCollection'>
            <li
                v-for="(colorArr,i) in colorList" :key='i'
                :style="{
                    color:colorArr[0]
                }"
            > {{ colorArr[1] }} </li>
        </ul>
    </div>
</template>


<script>
import {
    finalArr as colorList ,
} from '@/utils/color.js'

export default {
    data() {
        return {
            colorList,
            colorCtrlColor:'',
            hexColorCtrlColor:'',
            rgba:{
                red:0,
                green:0,
                blue:0,
                alpha:1,
            },
            rgb:{
                red:0,
                green:0,
                blue:0,
            },
        };
    },
    created(){

    },
    mounted(){

    },
    //props:{},//[]
    methods:{

    },
    computed:{

    },
    watch:{

    },
    filters:{

    },
    directives:{

    },
    components:{

    },
};
</script>