export default {
    data() {
      return {
        axiasWidth: 2,
        originPos: { x: 20, y: 20, lineColor: "#000" },
        coordOffset:{x:20,y:20},
        csRange: { width: 800 - 40, height: 600 - 40 },
        arrowSize: { length: 20, width: 5 },
      };
    },
    computed: {
        xCalibration(){
            let res=[]
            let title=40
            for(let i=this.originPos.x+title;i<this.originPos.x+this.csRange.width;i+=title){
                res.push({x:i,y:this.originPos.y,label:i-this.originPos.x})
            }
            return res;
        },
        yCalibration(){
            let res=[]
            let title=40
            for(let i=this.originPos.y+title;i<this.originPos.y+this.csRange.height;i+=title){
                res.push({x:this.originPos.x,y:i,label:i-this.originPos.y})
            }
            return res;
        },
        xEnd(){
            return {x:this.originPos.x + this.csRange.width,y:this.originPos.y}
        },
        yEnd(){
            return {x:this.originPos.x,y:this.originPos.y + this.csRange.height}
        },
        xArrow(){
            let posX=this.xEnd.x
            let posY=this.xEnd.y
            let legWidth=this.arrowSize.width / 2
            let footL=[posX - this.arrowSize.length , posY - legWidth]
            let footR=[posX - this.arrowSize.length , posY + legWidth]
            return [ footL,[posX,posY],footR ].map((m) => m.join(',')).join(' ')
        },
        yArrow(){
            let posX=this.yEnd.x
            let posY=this.yEnd.y
            let legWidth=this.arrowSize.width / 2
            let footL=[posX - legWidth , posY - this.arrowSize.length]
            let footR=[posX + legWidth , posY - this.arrowSize.length]
            return [ footL,[posX,posY],footR ].map((m) => m.join(',')).join(' ')
        },
    },
}