class WebSiteClcted {
    constructor({
        url='',
        title='',
        createTime='',
        icon='',
        type='',
    }){
        this.url = url || null;
        this.title = title || null;
        this.createTime = createTime || null;
        this.icon = icon || null;
        this.type = type || null;
    }
}

function dataHandler(data={}){
    let res=[]
    if(data.children&&data.children.length){
        for(let i=0;i<data.children.length;i++){
            res=[].concat(res,dataHandler(data.children[i]))
        }
    }else if(data.name==='a'){
        let cell={
            content:data.innerContent,
        }
        for(let i=0;i<data.props.length;i++){
            cell[data.props[i].name]=data.props[i].value
        }
        res.push(cell);
    }
    return res;
};
export let hrefList=dataHandler(collTree)
