export function turningPlate(startAt){
    let baseRing=12
    let minCount=1
    let maxCount=10
    let randomCount=Math.floor(
        Math.random() 
        * ( maxCount - minCount +1 ) 
        * baseRing 
        + minCount * baseRing 
        + startAt
    )
    return randomCount % baseRing +1

}
let mapOrder=[
    'start',
    '1','2',3,4,5,6,7,8,9,
    'prison',
    11,12,13,14,15,16,17,18,19,
    'parking',
    21,22,23,24,25,26,27,28,29,
    'arrest',
    31,32,33,34,35,36,37,38,39
]
mapOrder.fillStep=0
mapOrder.reFill=function(arr=[]){
    if(mapOrder.fillStep>=4){
        return;
    }
    if(arr.length!==36){
        if(arr.length===9){
            switch(mapOrder.fillStep){
                case 0 :
                    mapOrder.splice(1,9,...arr.splice(0,9))
                    mapOrder.fillStep++
                break;
                case 1 :
                    mapOrder.splice(11,9,...arr.splice(0,9))
                    mapOrder.fillStep++
                break;
                case 2 :
                    mapOrder.splice(21,9,...arr.splice(0,9))
                    mapOrder.fillStep++
                break;
                case 3 :
                    mapOrder.splice(31,9,...arr.splice(0,9))
                    mapOrder.fillStep++
                break;
            }
        }
        return;
    }
    mapOrder.splice(1,9,...arr.splice(0,9))
    mapOrder.fillStep++
    mapOrder.splice(11,9,...arr.splice(0,9))
    mapOrder.fillStep++
    mapOrder.splice(21,9,...arr.splice(0,9))
    mapOrder.fillStep++
    mapOrder.splice(31,9,...arr.splice(0,9))
    mapOrder.fillStep++
    // return mapOrder
}


function mortgageOfPrice(price){
    return {
        price,
        mortgage:price/2
    }
}
let trainStationRule={
    ...mortgageOfPrice(2000),
    rule(player){
        return player.trainStationList.length * 250
    }
}
let electricWaterRule={
    ...mortgageOfPrice(1500),
    rule(player){
        if(player.electricStation&&player.waterStation){
            return 'rand 100'
        }else{
            return 'rand 10'
        }
    }
}
