
<style scoped lang='scss'>
@import './index.scss';
</style>


<template>
    <div class='sheetCell'>
        <div class="cellTit">{{ name }}</div>
        <div class="cellCtt">
            <div class="itemsList">
                <div class="itemBox" v-for='(directLevel,index) in list' :key='index'>
                    <div class="directiveArea"> {{ directLevel.directive }} </div>
                    <div class="descriptionArea"> {{ directLevel.description }} </div>
                </div>
            </div>
        </div>
    </div>
</template>


<script>
// import testMixins from './testMixins.js'
export default {
    props:{
        name:{
            type:String,
            default:'',
        },
        list:{
            type:Array,
            default:[],
        }
    },
};
</script>