<style scope lang='scss'>
    ul {
        &,li {
            list-style: none;
        }
        &.horizon {
            display:flex;
            justify-content: space-around;
            &>li {
                &.menuItem {
                    cursor: pointer;
                    &.active {
                        font-weight: 700;
                        background: #ccc;
                    }
                }
            }
        }
    }
</style>
<template>
    <ul :class='{
        horizon:posMod==="horizon"
    }'>
        <li 
            v-for='(m,i) in menuList' :key='i'
           :class="'menuItem '+(activeIndex===i?'active':'')"
           @click.stop='onClickTheMenuItem(m,i)'
        >
            <span> {{ m.name }} </span>
            <!-- 组件自调用 需要组件name -->
            <routerCS v-if='m.children' :posMod='posMod' :menuList='m.children'  />
        </li>
    </ul>
</template>


<script>
    export default {
        name:'routerCS',
        data(){return {
            activeIndex:0
        }},
        props:{'menuList':{
            default:[]
        },'posMod':{
            default:'vertical'
        }},
        methods:{
            onClickTheMenuItem(m,i){
                this.activeIndex=i;
                this.$router.push(m.path)
            }
        }
    }
</script>