
<style scoped lang='scss'>
    .gameMap {
        position: relative;
        margin: 0 auto;
    }
    .mapCube {
        position: absolute;
        border: 1px solid #000;
        text-align: center;
        word-break: break-all;
        word-wrap: break-word;
        .playerBox {
            position: absolute;
            left: 0;
            top: 0;
            right: 0;
            bottom: 0;
            z-index: 40;
            background: rgba($color: #fff, $alpha: 0.8);
            &:after {
                content: '';
                width: 100%;
                height: 0;
                clear: both;
                overflow: hidden;
                display: block;
                visibility: hidden;
            }
            .playerCell {
                float: left;
                width: 24px;
                height: 24px;
                border-radius: 50%;
                text-align: center;
                line-height: 24px;
                border: 1px solid;
            }
        }
        .buildingBox {
            position: absolute;
            left: 0;
            top: 0;
            right: 0;
            bottom: 0;
            z-index: 20;
        }
    }
</style>


<template>
    <div>
        <div class="toolBar">
            <div class="randomPlate">
                <div>
                    {{ randPot }}
                </div>
                <button @click='()=>turningPlate(randPot)'>掷点</button>
            </div>
            <div class="turnOrder">
                <div>

                    <div :style="{
                        color:activePlayer.sign
                    }"> {{ activePlayer.name }} </div>
                    <div> {{ activePlayer.cash }} </div>
                    <div> {{ activePlayer.stableProperty || '无地产' }} </div>
                    <div> {{ activePlayer.pos }} </div>
                    <div> {{ activePlayer.abnormalStatus || '正常' }} </div>
                </div>
            </div>
            <div class="calc">
                <div class="topInCash"> 最富之人 {{ topInCash.name }} : {{ topInCash.count }} </div>
                <div class="bottomInCash"> 最穷之人(未破产) {{ bottomInCash.name }} : {{ bottomInCash.count }} </div>
            </div>
            <div class="turnDetail">

            </div>
            <div class="ctrl">
                <button @click='aRound'>执行回合</button>
            </div>
        </div>
        
        <div class="gameMap" :style='"height:"+mapHeight+";width:"+mapWidth'>
            <div class="playerCell"
                :style="{

                }"
            ></div>
            <div class="mapCube"
                v-for='(cube,i) in mapOrderList' :key='i'
                :title='cube.type'
                :style="mapPoser(i)"
            >
                {{ cube.name }}
                <div class="playerBox"
                    v-if='anyPlayerHere(i)'
                >
                    <div class="playerCell"
                        v-for='(player,playerIndex) in playerList' 
                        :key='playerIndex'
                        v-show='player.pos===i'
                        :style="{
                            color:player.sign,
                            'border-color':player.sign
                        }"
                    >
                        {{ player.name }}
                    </div>
                </div>
                <div class="buildingBox"></div>
            </div>
        </div>
    </div>
</template>


<script>
import mapOrder,{
    playerList ,
    potList ,
    turningPlate ,
    chanceList ,
    fateList ,
} from './content/index'
export default {
    data() {
        return {
            randPot:1,
            nowturn:0,
            playerList:[],
            potList:[],
            mapOrderList:[],
            mapCubeWidth:80,
            mapCubeHeight:60,
            
        };
    },
    created(){
        this.resetGame()
    },
    computed:{
        activePlayer(){
            return this.playerList[this.nowturn]
        },
        mapWidth(){
            return 10 * this.mapCubeWidth + 10 * 9 + 'px'
        },
        mapHeight(){
            return 10 * this.mapCubeHeight + 10 * 9 + 'px'
        },
        topInHouseCount(){
            return {
                name:'',
                count:'',
            }
        },
        topInCash(){
            let res={count:0}
            for(let i=this.playerList.length;i--;){
                let thePlayer=this.playerList[i];
                let count=res.count
                if(count<thePlayer.cash){
                    res.count=thePlayer.cash
                    res.name=thePlayer.name
                }
            }
            let theOnes=this.playerList.filter(({cash})=>cash==res.count)
            if(theOnes.length>1){
                res.name=theOnes.map(_=>_.name).join(',')
            }
            return res
        },
        bottomInCash(){
            let res={count:800_0000}
            let thePlayers=this.playerList.filter(({status})=>status!=='bankruptcy');
            for(let i=thePlayers.length;i--;){
                let thePlayer=thePlayers[i];
                let count=res.count
                if(count>thePlayer.cash){
                    res.count=thePlayer.cash
                    res.name=thePlayer.name
                }
            }
            let theOnes=thePlayers.filter(({cash})=>cash==res.count)
            if(theOnes.length>1){
                res.name=theOnes.map(_=>_.name).join(',')
            }
            return res
        },
    },
    methods:{
        whoHasMostCash(){
            let playerIndexList=[];
            let richCash=Math.max(...this.playerList.map(m=>m.cash))
            for(let index=this.playerList.length;index--;){
                let thePlayer=this.playerList[index];
                if(thePlayer.cash===richCash){
                    playerIndexList.push(index)
                }
            }
            return playerIndexList
        },
        whoHasLeastCash(){
            let playerIndexList=[];
            let playerCashOnGame=this.playerList.filter(m=>m.cash>=0).map(m=>m.cash)
            let poorCash=Math.min(...playerCashOnGame)
            for(let index=this.playerList.length;index--;){
                let thePlayer=this.playerList[index];
                if(thePlayer.cash===poorCash){
                    playerIndexList.push(index)
                }
            }
            return playerIndexList
        },
        whoHasMostHouse(){
            let playerIndexList=[];
            return playerIndexList
        },
        whoHasLeastHouse(){
            let playerIndexList=[];
            return playerIndexList
        },
        whoIsNearestTo(aimPos){
            let playerIndexList=[];
            return playerIndexList
        },
        resetGame(){
            this.playerList=playerList
            this.potList=potList
            this.mapOrderList=mapOrder.map(code=>{
                return potList.find(pot=>{
                    return pot.code===code
                })
            })
        },

        aRound(){
            // 轮序 0  玩家列表 位置 起点 
            // 开始游戏 轮序 0 对应玩家 掷点
            let activePlayer=this.playerList[this.nowturn]
            let checkNormal = activePlayer.beforeRound()
            if(!checkNormal){
                this.nextTurn();
                return;
            }
            let moveSpan = turningPlate(1)
            activePlayer.moveAcross(moveSpan)
            let aimPos=activePlayer.pos;
            let aimPot=this.mapOrderList[aimPos]
            let aimOwner=aimPot.owner
            switch(aimPot.type){
                case 'land':
                    if(aimOwner!=='bank'){
                        if(aimOwner===activePlayer.name){
                            if(aimPot.houseCount!='hotel'){
                                let housePrice=aimPot.housePrice
                                if(activePlayer.cash>=housePrice){
                                    let wantBuild=confirm('加盖房屋吗')
                                    if(wantBuild){
                                        activePlayer.lostMoney(housePrice)
                                        // let theProperty = activePlayer.stableProperty.find(property=>{
                                        //     return property.code===aimPot.code;
                                        // })
                                        // theProperty.houseCount
                                        aimPot.addBuilding();
                                    }
                                }
                            }
                        }else{
                            let landOwner=this.playerList.find(player=>{
                                return player.name === aimOwner
                            })
                            let toll
                            if(aimPot.houseCount){
                                toll = aimPot.toll()
                            }else{
                                let lackSomeInColor = this.mapOrderList.filter(mapCell=>{
                                    return mapCell.type=='land' && mapCell.color==aimPot.color
                                }).some(mapCell=>{
                                    return mapCell.owner!=aimOwner
                                })
                                if(lackSomeInColor){
                                    toll = aimPot.toll()
                                }else{
                                    toll = aimPot.toll(1)
                                }
                            }
                            activePlayer.lostMoney(toll)
                            landOwner.getMoney(toll);
                            landOwner=null;
                        }
                    }else{
                        let price = aimPot.price;
                        if(activePlayer.cash>=price){
                            let wantBuyLand=confirm('买地吗');
                            if(wantBuyLand){
                                activePlayer.lostMoney(price);
                                // activePlayer.stableProperty.push({
                                //     name:aimPot.name,
                                //     type:aimPot.type,
                                //     color:aimPot.color,
                                //     houseCount:0
                                // })
                                aimPot.ownTo(activePlayer.name);
                            }
                        }
                    }
                break;
                case 'station':
                    if(aimOwner!=='bank'){
                        if(aimOwner===activePlayer.name){

                        }else{
                            
                            let landOwner=this.playerList.find(player=>{
                                return player.name === aimOwner
                            })
                            let stationCount=this.mapOrderList.filter(mapCell=>{
                                return mapCell.type=='station'&& mapCell.owner===landOwner
                            }).length
                            let toll=aimPot.toll(stationCount)
                            activePlayer.lostMoney(toll)
                            landOwner.getMoney(toll);
                            landOwner=null;
                        }
                    }else{
                        let price=aimPot.price
                        if(activePlayer.cash>=price){
                            let wantBuyStation=confirm('买车站吗')
                            if(wantBuyStation){
                                activePlayer.lostMoney(price);
                                aimPot.ownTo(activePlayer.name);
                            }
                        }
                    }
                break;
                case 'company':
                    if(aimOwner!=='bank'){
                        if(aimOwner===activePlayer.name){

                        }else{
                            
                            let landOwner=this.playerList.find(player=>{
                                return player.name === aimOwner
                            })
                            let stationCount=this.mapOrderList.filter(mapCell=>{
                                return  mapCell.type=='company' && mapCell.owner===landOwner
                            }).length
                            let times=1
                            if(stationCount==2){
                                times=100
                            }else{
                                times=10
                            }
                            let toll=turningPlate() * times
                            activePlayer.lostMoney(toll)
                            landOwner.getMoney(toll);
                            landOwner=null;
                        }
                    }else{
                        let price=aimPot.price
                        if(activePlayer.cash>=price){
                            let wantBuyStation=confirm('买公司吗')
                            if(wantBuyStation){
                                activePlayer.lostMoney(price);
                                aimPot.ownTo(activePlayer.name);
                            }
                        }
                    }
                break;
                case 'chance':
                break;
                case 'fate':
                break;
                case 'stopPot':
                    activePlayer.getInStop()
                    break
                case 'fine':
                    activePlayer.lostMoney(aimPot.fine)
                    break
                case 'arrest':
                    activePlayer.arrested()
                    break
                default :
                    break
            }
            this.nextTurn()
            return
        },
        startGame(){

        },
        /**转盘 */
        turningPlate(startAt=1){
            this.randPot = turningPlate(startAt)
            
        },
        anyPlayerHere(pos){
            return this.playerList.some(player=>{
                return player.pos===pos
            })
        },
        whoOwnHere(pos){

        },
        nextTurn(){
            if(this.nowturn+1>=this.playerList.length){
                this.nowturn=0
            }else{
                this.nowturn++
            }
        },
        mapPoser(pos){
            let piecePos=(pos%10)
            let pieceIndexPos=Math.floor(pos/10)
            let left,top;
            let size={
                width:this.mapCubeWidth + 'px',
                height:this.mapCubeHeight + 'px',
            };
            switch(pieceIndexPos){
                case 0 :
                    left=piecePos * this.mapCubeWidth +piecePos * 5 + 'px';
                    top=0
                return {left,top,...size}
                case 1:
                    left=10 * this.mapCubeWidth + 10 * 5 + 'px';
                    top=piecePos * this.mapCubeHeight+piecePos * 5 + 'px';
                return {left,top,...size}
                case 2:
                    left=(10-piecePos) * this.mapCubeWidth +(10-piecePos) * 5 + 'px';
                    top=10 * this.mapCubeHeight+ 10 * 5 +'px'
                return {left,top,...size}
                case 3:
                    left=0
                    top=(10-piecePos) * this.mapCubeHeight+(10-piecePos) * 5 + 'px';
                return {left,top,...size}
            }
        },
        /**奖励 */
        reward(player,howMuch){
            player.cash+=howMuch
        },
        /**罚款 */
        fine(player,howMuch){
            player.cash-=howMuch
        },
        /**罚站 */
        stop(player){
            if(player.abnormalStatus){
                if(player.abnormalStatus.stop){
                    player.abnormalStatus.stop++
                }else{
                    player.abnormalStatus.stop=1
                }
            }else{
                player.abnormalStatus={
                    stop:1,
                }
            }
        },
        arrest(player){
            if(player.abnormalStatus){
                if(player.abnormalStatus.stop){
                    player.abnormalStatus.stop++
                }else{
                    player.abnormalStatus.stop=1
                }
                if(player.abnormalStatus.badGuy){
                    player.abnormalStatus.badGuy++
                }else{
                    player.abnormalStatus.badGuy=1
                }
            }else{
                player.abnormalStatus={
                    stop:1,
                    badGuy:1,
                }
            }
        },
        /**过路费 */
        toll(custom,owner,cost){
            custom.cash-=cost;
            owner.cash+=cost;
        },
        /**盖房 | 旅馆 */
        building(player,place){

        },
        /**抵押 */
        mortgage(player,place){

        },
        /**破产 */
        bankruptcy(player){

        },
        /**交易 */
        trade(seller,buyer,item){

        },
        /**移动 */
        move(nowPos,aimPos){

        },
        /**买地 */
        buy(player,place){
            if(player.cash>=place.worth){

            }
        },
        /**坐牢 */
        goIntoPrison(player){

        },
    },
};
</script>