export default {
    data() {
      return {
          lineX1:50,
          lineY1:50,
          lineX2:450,
          lineY2:450,
          lineW:2,
          lineColor:'#efa3b1',
          lineFill:'#a3ef5c',
  
      };
    },
    methods: {
        resetLine(){
          this.lineX1 = 50  ;
          this.lineY1 = 50  ;
          this.lineX2 = 450 ;
          this.lineY2 = 450 ;
          this.lineW  = 2   ;
          this.lineColor = '#efa3b1';
          this.lineFill = '#a3ef5c';
        },
    },
}