let expTypeList = [

    {
        name: "SSR",
        pow: 0.1
    },
    {
        name: "SR",
        pow: 0.5
    },
    {
        name: "R",
        pow: 5
    },
    {
        name: "N",
        pow: 20
    },
    {
        name: "SN",
        pow: 30,
    },
    {
        name: "SSN",
        pow: 'rest'
    },
].filter(m=>m);
let powSetList = expTypeList.filter(m => m.pow !== 'rest');
let powSetSum = powSetList.reduce((s, n) => {
    return (s.pow || s) + n.pow
})
let powRest = 100 - powSetSum;
let powUnsetOneIndex = expTypeList.findIndex(m => m.pow === 'rest')
expTypeList[powUnsetOneIndex].pow = powRest;
let powRangeToolCount = 0;
for (let typeIndex = 0; typeIndex < expTypeList.length; typeIndex++) {
    expTypeList[typeIndex].posRange = [powRangeToolCount, powRangeToolCount + expTypeList[typeIndex].pow];
    powRangeToolCount += expTypeList[typeIndex].pow;
}
console.log('--expTypeList---', expTypeList)

let randOffsetTool = expTypeList.filter(m => {
    return m.pow.toString().includes('.');
}).map(m => {
    return m.pow.toString().split('.')[1].length;
})
/** 最小落点区容量修正补偿 */
let amendOffset = 10
let maxDivideNumLength = Math.max(...randOffsetTool)
/** 小数补偿  */
let randOffsetNum = Math.pow(10, maxDivideNumLength) * amendOffset
console.log('---randOffsetNum--', randOffsetNum)

/** 第一层随机 借此确定抽到哪类卡 */
export function randCard_one() {
    return Math.floor(Math.random() * 100 * randOffsetNum) / randOffsetNum;
}
/** 第二层随机 如果第一随机数落在有up项的类中 以此进一步确定此类中落点是否在up区  */
export function randCard_two() {
    return Math.floor(Math.random() * 2)
}

/**非酋补偿 */
let unluckyOffsetCount = 0;
function unluckyCount() {
    if (unluckyOffsetCount >= 100) {
        let shouldPosUp = randCard_two()
        unluckyOffsetCount = 0
        return expDataList.filter(m => m.type === 'SSR' && (
            zeroAndOne(m.isRateUp,shouldPosUp)
        ))
    } else {
        unluckyOffsetCount += 1
    }
}
/** 0 1 对调控制 实际就是个同或运算 */
function zeroAndOne(status=0,shoudldKeep=1){
    return Math.abs(status - 0.5 + 0.5 * Math.pow(-1,shoudldKeep + 1))
}
/**最终函数 */
export function landTheRateNum() {
    let rateNum = randCard_one();
    for (let i = 0; i < expTypeList.length; i++) {
        if (rateNum >= expTypeList[i].posRange[0] && rateNum < expTypeList[i].posRange[1]) {
            let typeName = expTypeList[i].name;
            let theTypeCellList = expDataList.filter(m => {
                return m.type === typeName
            })
            let offsetList = unluckyCount()
            if (theTypeCellList.find(m => m.isRateUp)) {
                let shouldPosUp = randCard_two()
                let areaCellList = theTypeCellList.filter(m => {
                    return zeroAndOne(m.isRateUp,shouldPosUp)
                })
                let theOne = randArr(areaCellList)
                if (theOne.type === 'SSR') {
                    unluckyOffsetCount = 0;
                    return theOne
                } else {
                    if (offsetList) {
                        return randArr(offsetList)
                    } else {
                        return theOne
                    }
                }
            } else {
                let theOne = randArr(theTypeCellList)
                if (theOne.type === 'SSR') {
                    unluckyOffsetCount = 0;
                    return theOne
                } else {
                    if (offsetList) {
                        return randArr(offsetList)
                    } else {
                        return theOne
                    }
                }
            }
        }
    }
}