
<style scoped lang='scss'>
	#myCanvas{
		border:1px solid black;
	}
</style>


<template>
    <div>
	<canvas ref='myCanvas' id="myCanvas" 
        :width="cvsWidth" 
        :height="cvsHeight"
        @mousedown='start'
        @mousemove='draw'
        @mouseup='finish'
    ></canvas>
	<p>画笔颜色：</p>
	<button @click="()=>setPenColor('yellow')">黄色</button>
	<button @click="()=>setPenColor('black')">黑色</button>
	<button @click="()=>setPenColor('red')">红色</button>
	<button @click="()=>setPenColor('green')">绿色</button>
	<button @click="()=>setPenColor('blue')">蓝色</button>
	<button @click="()=>setPenColor('pink')">粉色</button>
	<button @click="()=>setPenColor('orange')">橙色</button>
	<p>画笔粗细：</p>
	<button @click="()=>setPenWidth('thin')">细画笔</button>
	<button @click="()=>setPenWidth('mid')">中画笔</button>
	<button @click="()=>setPenWidth('thick')">粗画笔</button>
    </div>
</template>


<script>
export default {
    data() {
        return {
            cvsWidth:860,
            cvsHeight:480,
            isMousePress:false,
            last:null,
            pen:{},
            offset:{

            }
        };
    },
    created(){
        
    },
    mounted(){
        this.initCanvas()
    },
    //props:{},//[]
    methods:{
        pos(event){//鼠标取得点
            var ex,ey;
            ex=event.clientX;
            ey=event.clientY;

            if(this.offset.top){
                ey-=this.offset.top
            }
            if(this.offset.left){
                ex-=this.offset.left
            }
            return{
                x:ex,
                y:ey
            }
        },
        initCanvas(){
            let cvs=this.$refs.myCanvas;
            let rectSizer= cvs.getBoundingClientRect()
            this.offset.top=rectSizer.top;
            this.offset.left=rectSizer.left;
            this.pen=cvs.getContext('2d');
            this.pen.lineWidth=1
            this.pen.strokeStyle='blue'
        },
        start(){
            this.isMousePress=true;
        },
        draw(event){
            if(!this.isMousePress){ return};
            var xy=this.pos(event);
            if(this.last!=null){//看结束点是否为空
                //不为空证明有开始点结束点，方才开始绘图
                this.pen.beginPath();
                this.pen.moveTo(this.last.x,this.last.y);//上次绘图停止的点
                this.pen.lineTo(xy.x,xy.y);//本次绘图的终点
                this.pen.stroke();
            }
            this.last=xy;//把本次绘图终点变为上次绘图的重点
        },
        finish(){
            this.isMousePress=false;
            this.last=null;
        },
        setPenWidth(lv='thin'){
            switch(lv){
                case 'thin':this.$set(this.pen,'lineWidth',1);break;
                case 'mid':this.$set(this.pen,'lineWidth',5);break;
                case 'thick':this.$set(this.pen,'lineWidth',10);break;
            }
        },
        setPenColor(color='black'){
            this.$set(this.pen,'strokeStyle',color);
        },
    },
};
</script>