
<style scoped lang='scss'>
@import '~@/styles/dataExtender.scss';
</style>


<template>
    <div class="wrapper">
        <div class="titleBox">数据速生器</div>
        <div class="inputArea">
            <div class="formCell flexBox justBetween">
                <div class="formLabel">数据形式</div>
                <textarea class="dataFormat" v-model='form.dataFormat'></textarea>
            </div>
            <!-- <div class="formCell flexBox justBetween">
                <div class="formLabel">规则说明</div>
                <div></div>
            </div> -->
            <div class="formCell flexBox justBetween">
                <div class="formLabel">编号随机</div>
                <div class="randChecker flexBox justBetween itemCeter">
                    <input type="radio" name='randNo' v-model='form.randChecker' :value='one'><span>使用随机的追加编码</span>
                    <input type="radio" name='randNo' v-model='form.randChecker' :value='zero' checked='checked'><span>使用默认的追加编码</span>
                </div>
            </div>
            <div class="formCell flexBox justBetween" id='randPower'>
                <div class="formLabel">进制选择</div>
                <div class="randChecker flexBox justBetween itemCeter">
                    <input type="radio" name='randPow' v-model='form.formatChecker' :value='one'><span>十六进制</span>
                    <input type="radio" name='randPow' v-model='form.formatChecker' :value='zero' checked='checked'><span>十进制 (默认)</span>
                </div>
            </div>
            <div class="formCell flexBox justBetween">
                <div class="formLabel">生成数量</div>
                <input type="number" class="dataLength" v-model='form.dataLength'>
            </div>
            <div class="formCell">
                <input type="button" class="makeSure" @click="makeSure" value='生成数据' />
            </div>
        </div>
        <div class="dataShower" >
            <div v-if="dataShower">
                {{dataShower?'[':''}}
                <div class="blockquoter">
                    <ul class="dataBoard" >
                        <li
                            v-for="(m,i) in dataShower" :key='i'
                            v-html='m+((dataShower.length-1===i)?"":",")'
                        ></li>
                    </ul>
                </div>
                {{dataShower?']':''}}
            </div>
        </div>
    </div>
</template>


<script>
function randNum(k,hex=false){
    let p=hex?'0x1':'1'
    let base=Number(p.padEnd(p.length+k,'0'));
    return Math.floor(Math.random()*base).toString(hex?16:10).padStart(k,'0')
}
export default {
    data() {
        return {
            zero:'0',
            one:'1',
            form:{
                dataFormat:'',
                randChecker:'0',
                dataLength:1,
                formatChecker:'0'
            },
            dataShower:''
        };
    },
    created(){
        
    },
    mounted(){
        
    },
    //props:{},//[]
    methods:{
        renderData({dataFormat,dataLength,randChecker,formatChecker}){
            let data=[];
            let dataCb=null;
            let objExp=/^\w+:.+(,\w+:.+)*$/g
            let isObj=objExp.test(dataFormat)
            if(randChecker==='1'){
                let pow=false
                if(formatChecker==='1'){
                    pow=true
                }
                dataCb=()=>{
                    return dataFormat.replace(/\$+/g,$match=>{
                        return randNum($match.length,pow)
                    })
                }
            }else{
                dataCb=(num)=>{
                    return dataFormat.replace(/\$+/g,$match=>{
                        return num.toString().padStart($match.length,'0')
                    })
                }
            }
            if(dataLength){
                let borderL='';
                let borderR=''
                if(isObj){
                    borderL='{'
                    borderR='}'
                }
                for(let x=0;x<dataLength;x++){
                    data.push(`${borderL}${dataCb(x+1)}${borderR}`)
                }
                
            }
            return data
        },
        makeSure(){
            this.dataShower=this.renderData(this.form)
        }
    },
    computed:{
        
    },
    watch:{
        
    },
    filters:{
        
    },
    directives:{
        
    },
    components:{
        
    },
};
</script>