
export function getTime(t=new Date(),needWeek=false){
    let [yyyy,MM,dd,hh,mm,ss]=[
        t.getFullYear(),
        t.getMonth()+1,
        t.getDate(),
        t.getHours(),
        t.getMinutes(),
        t.getSeconds()
    ].map(function(m){
        return (''+m).padStart(2,'0')
    });
    if(needWeek){
        let w=t.getDay().toString();
        return {yyyy,MM,dd,hh,mm,ss,w}
    }else{
        return {yyyy,MM,dd,hh,mm,ss}
    }
}

export function getFutString(t=new Date()){
    let {yyyy,MM,dd,hh,mm,ss}=getTime(t);
    return `${yyyy}-${MM}-${dd} ${hh}:${mm}:${ss}`
}
export function deltaTimeGetter(block=()=>{},times=1000){
    let t0=new Date()
    for(let i=0;i<times;i++){
        block()
    }
    let t1=new Date()
    console.log((t1.getTime()-t0.getTime()) / times)
    return (t1.getTime()-t0.getTime()) / times
}

/**分秒计时器构建器 */
export function timeMechine(){
    let res={time:null}
    res.secHead=0;
    res.minHead=0;
    res.value='0:00'
    res.status=0
    res.start=function(){
        res.status=1
        res.timer=setInterval(()=>{
            res.secHead+=1;
            if(res.secHead>=60){
                res.secHead=0
                res.minHead+=1
            }
            res.value=`${res.minHead}:${res.secHead.toString().padStart(2,'0')}`
            // console.log('--持续赋值--',res)
        },1000)
        res.stop=function(){
            if(res.status){
                res.status=2
                clearInterval(res.timer)
                // console.log('---暂停----',res)
                return res;
            }
        }
        res.clear=function(){
            if(res.status){
                let result=res.value;
                if(res.status===1){
                    clearInterval(res.timer)
                }
                res.secHead=0;
                res.minHead=0;
                res.value='0:00'
                res.status=0
                // console.log('---清空---',res)
                return result;
            }
        }
    }
    // console.log('----对象生成包定义完成-----',res)
    return res;
}