
export function camel2underline(camelStr){
    return camelStr.replace(/[A-Z]/g,$match=>{
        return `_${$match.toLowerCase()}`
    })
}
// console.log(camel2underline('asdFqweFqw'))
export function underline2camel(underlineStr){
    return underlineStr.replace(/_[a-z]/g,$match=>{
        return $match[1].toUpperCase()
    })
}
// console.log(underline2camel('asd_qwe_sasd_asd'))
export function checkFalseValue(falseValue){
    switch(falseValue){
        case null:
            return 'null';
        case 0:
            return '0';
        case '':
            return 'empty';
        case undefined:
            return 'undefined';
        case false:
            return 'false';
    }
}
export function checkNull(value){
    if(!value){
        switch(value){
            case null:return 'NULL';
            case undefined:return 'NULL';
            case '':return "''";
            default:return value;
        }
    }else{
        let valueType=typeof(value);
        switch(valueType){
            case 'string':return `'${value}'`;
            default:return value;
        }
    }

}
export function Obj2SQLstr(obj={}){
    let keyNameList=Object.getOwnPropertyNames(obj)
    let keyNamePart=`(${keyNameList.map(keyName=>{
        return "`"+camel2underline(keyName)+"`"
    }).join(', ')})`
    let valuePart=`(${keyNameList.map(keyName=>{
        let value=obj[keyName]
        return checkNull(value)
    }).join(', ')})`
    return `${keyNamePart} VALUES ${valuePart};`
}
export function insertInto(db='',sheet='',keyObj={}){
    return `INSERT INTO \`${db}\`.\`${sheet}\`${Obj2SQLstr(keyObj)}`
}