/**简易深拷贝 */
export function JSONdeep(any) {
  return JSON.parse(JSON.stringify(any))
}
/** 获取脱敏的路由信息列表 */
export function getRouteInfoList(routeList) {
  let res = []
  for (let head = 0; head < routeList.length; head++) {
    if (routeList[head]) {
      let routeInfo = {}
      for (let key in routeList[head]) {
        if (key !== 'component' && key !== 'children') {
          routeInfo[key] = JSONdeep(routeList[head][key])
        }
      }
      if (routeList[head].children && routeList[head].children.length) {
        routeInfo.children = getRouteInfoList(routeList[head].children)
      }
      res.push(routeInfo)
    }
  }
  return res;
}

/**css对象转为css字串 */
export function turnCssObject2CssString(cssObj = {}) {
  let cssProList = Object.getOwnPropertyNames(cssObj || {});
  let res = ''
  for (let cssProName of cssProList) {
    res += `${cssProName}:${cssObj[cssProName]};`;
  }
  return res;
}

export function downLoad(url) {
  var toolEl = document.createElement("a");
  toolEl.download = '页面快照'; // 设置下载的文件名，默认是'下载'
  toolEl.href = url;
  document.body.appendChild(toolEl);
  toolEl.click();
  toolEl.remove(); // 下载之后把创建的元素删除
}

export function conof(any) {
  return Object.prototype.toString.call(any).slice(1, -1).split(' ')[1].toLowerCase()
}

export function matchProByAttr(list = [], options = {
  tipPro: 'key',
  tipValue: 'any',
  tarPro: 'key',
}) {
  let theOne = list.find(listItem => {
    return listItem[options.tipPro] == options.tipValue
  })
  return theOne ? theOne[tarPro] : null;

}

export function randArr(arr=[]){
  let len=arr.length;
  return Math.floor(Math.random()*len);
}
function messArr(arr=[]){
  let arrCahe=JSON.parse(JSON.stringify(arr))
  let res=[]
  let len=arrCahe.length;
  for(let i=len;i--;){
    if(i===1){
      res.push(arrCahe[0])
      arrCahe=null;
    }else if(i>1){
      let index = Math.floor(Math.random()*i)
      res.push(...arrCahe.splice(index,1))
    }else{
      break
    }
  }
  return res
}
