export default {
    data() {
      return {
          circleCenterX:50,
          circleCenterY:50,
          circleR:20,
          circleBorder:2,
          circleBorderColor:'#abe3fe',
          circleFill:'#2d3efb'
  
      };
    },
    methods: {
        resetCircle(){
            this.circleCenterX=50;
            this.circleCenterY=50;
            this.circleBorder=2;
            this.circleBorderColor='#abe3fe';
            this.circleR=20;
            this.circleFill='#2d3efb';
        },
    },
}