
<style scoped lang='scss'>
    .sheetWrapper {
        
        background: #202f32;
        min-height: 100%;
        .sheetBody {
            border-top: 0.06 * 120px dashed #eada9b;
            border-bottom: 0.06 * 120px dashed #eada9b;
            height: auto;
        }
    }
</style>


<template>
    <div class='sheetWrapper'>
        <div class="sheetBody">
            <waterFall 
                :dataList='sectionList'
            >
                <template slot-scope='scope'>
                    <div>
                    <sheetCell 
                        :name='scope.dataLevel.name'
                        :list='scope.dataLevel.list'
                    />
                    </div>
                </template>
            </waterFall>
        </div>
    </div>
</template>


<script>
import contentMixins from './content'
export default {
    mixins:[contentMixins],
};
</script>