<style scoped lang='scss'>
    .waterfallContainer {
        position: relative;
        /* max-width: 600px;
        margin: 0 auto; */
        // padding:0 50px;
    }

    .waterfallContainer .waterfallSlotContainer {
        position: absolute;
        width: calc(50vw - 30px);
        /* 布局transition效果 */
        transition: all .4s;
        word-break: break-all;
    }
    .waterfallContainer .waterfallSlotContainer>* {
        border:none;
        width: 95%;
    }
</style>

<template>
    <div class='waterfallContainer' ref='container'>
        <div class="waterfallSlotContainer"
            v-for="(dataLevel,index) in dataList" :key='index'
            :ref='"slot_"+index'
        >
            <slot :dataLevel='dataLevel' ></slot>
        </div>
    </div>
</template>

<script>
function qs(es){return document.querySelector(es)}
function qa(ea){return document.querySelectorAll(ea)}
    export default {
        data(){return {
            imgWidth:0,
            colCount:1,
            colHeightArry:[],
        }},
        props:{
            dataList:{type:Array,default:[]},

        },
        created(){
            // console.log('-----waterfall-----')
        },
        mounted(){
            let colCount;
            let colHeightArry=[];
            let imgWidth= qs('.waterfallContainer .waterfallSlotContainer').getBoundingClientRect().width;
            colCount=Math.floor(qs(".waterfallContainer").clientWidth / imgWidth);
            for(let colIndex=0;colIndex<colCount;colIndex++){
                colHeightArry[colIndex]=0;
            }
            qs('.waterfallContainer').addEventListener('load',(e)=>{
                if(e.target.className==='waterfallSlotContainer'){
                    let minValue=colHeightArry[0];
                    let minIndex=0;
                    for(let colIndex=0;colIndex<colCount;colIndex++){
                        if(colHeightArry[colIndex]<minValue){
                            minValue=colHeightArry[colIndex];
                            minIndex=colIndex;
                        }
                    }
                    e.target.style.left=minIndex * (imgWidth) + 'px';
                    e.target.style.top=minValue  + 'px';
                    colHeightArry[minIndex]+=e.target.clientHeight;
                    qs('.waterfallContainer').style.height=Math.max(...colHeightArry) + 'px'
                    // this.$forceUpdated()
                }
            },false)
            
            function reset(){
                colHeightArry = []
                colCount =Math.floor(qs(".waterfallContainer").clientWidth / imgWidth);
                for (let i = 0; i < colCount; i++) {
                    colHeightArry[i] = 0
                }
                let imgs= [...qa('.waterfallContainer .waterfallSlotContainer')];
                imgs.forEach((elLevel,index)=>{
                    let minValue=colHeightArry[0];
                    let minIndex=0;
                    for(let colIndex=0;colIndex<colCount;colIndex++){
                        if(colHeightArry[colIndex]<minValue){
                            minValue=colHeightArry[colIndex];
                            minIndex=colIndex;
                        }
                    }
                    elLevel.style.left=minIndex * (imgWidth)+ 'px';
                    elLevel.style.top=minValue+ 'px';
                    colHeightArry[minIndex]+=elLevel.clientHeight;
                    qs('.waterfallContainer').style.height=Math.max(...colHeightArry) + 'px'
                    // this.$forceUpdated()
                })
            }
            reset()
            window.addEventListener('resize',reset,false)
            window.addEventListener('load',reset,false)
        },
    }
</script>