
<style scoped lang='scss'>
.canvasContainer {
  width: 800px;

  margin: 0 auto;
  padding: 0;
}
.canvas {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 600px;
  transform: rotateX(180deg);
}
.ctrl {
    display: flex;
    justify-content: space-around;
}
</style>


<template>
  <div class="canvasContainer">
      <div class="ctrl">
          <div class="lineCtrl">
              <h5> 直线段 </h5>
              <div class="formItem">
                  <label for="lineCell_x1">X1 : </label><input id='lineCell_x1' v-model.number='lineX1'  type="number">
              </div>
              <div class="formItem">
                  <label for="lineCell_y1">Y1 : </label><input id='lineCell_y1' v-model.number='lineY1'  type="number">
              </div>
              <div class="formItem">
                  <label for="lineCell_x2">X2 : </label><input id='lineCell_x2' v-model.number='lineX2'  type="number">
              </div>
              <div class="formItem">
                  <label for="lineCell_y2">Y2 : </label><input id='lineCell_y2' v-model.number='lineY2'  type="number">
              </div>
              <div class="formItem">
                  <label for="lineCell_W">W : </label><input id='lineCell_W' v-model.number='lineW'  type="number">
              </div>
              <div class="formItem">
                  <label for="lineCell_Color">Color : </label><input id='lineCell_Color' v-model='lineColor'  type="color">
              </div>
              <div class="formItem">
                  <input id='lineCell_reset' @click='resetLine' type="button" value='重置线段'>
              </div>
          </div>
          <div class="circleCtrl">
              <h5> 圆 </h5>
              <div class="formItem">
                  <label for="circle_center_x">CX : </label><input id='circle_center_x' v-model.number='circleCenterX'  type="number">
              </div>
              <div class="formItem">
                  <label for="circle_center_y">CY : </label><input id='circle_center_y' v-model.number='circleCenterY'  type="number">
              </div>
              <div class="formItem">
                  <label for="circle_radius">R : </label><input id='circle_radius' v-model.number='circleR'  type="number">
              </div>
              <div class="formItem">
                  <label for="circle_border">Border : </label><input id='circle_border' v-model.number='circleBorder'  type="number">
              </div>
              <div class="formItem">
                  <label for="circle_fill">Fill : </label><input id='circle_fill' v-model='circleFill'  type="color">
              </div>
              <div class="formItem">
                  <label for="circle_borderColor">BorderColor : </label><input id='circle_borderColor' v-model='circleBorderColor'  type="color">
              </div>
              <div class="formItem">
                  <input id='circleCell_reset' @click='resetCircle' type="button" value='重置圆'>
              </div>
          </div>
      </div>
      <!-- svg 作图原点 左上角 x轴正方向朝右 y轴正方向朝下  -->
    <svg class="canvas">
        <g class='coordinateSystem'>
            <line
                class="yAxias"
                :stroke="originPos.lineColor"
                :stroke-width="axiasWidth"
                :x1="originPos.x"
                :y1="originPos.y"
                :x2="yEnd.x"
                :y2="yEnd.y"
            ></line>
            <polyline
                class="yArrow"
                style="fill: none; stroke: black; stroke-width: 2"
                :points="yArrow"
            ></polyline>
            <line
                class="xAxias"
                :stroke="originPos.lineColor"
                :stroke-width="axiasWidth"
                :x1="originPos.x"
                :y1="originPos.y"
                :x2="xEnd.x"
                :y2="xEnd.y"
            ></line>
            <polyline
                class="xArrow"
                style="fill: none; stroke: black; stroke-width: 2"
                :points="xArrow"
            ></polyline>
            <circle
                class="originPot"
                :cx="originPos.x"
                :cy="originPos.y"
                r="4"
            ></circle>
            <circle
                class='xCalibration'
                r="3"
                v-for="(xx,i) in xCalibration" :key='"xc_"+i'
                :cx="xx.x"
                :cy="xx.y"
                :title='xx.label'
            ></circle>
            <circle
                class='yCalibration'
                r="3"
                v-for="(yy,i) in yCalibration" :key='"yc_"+i'
                :cx="yy.x"
                :cy="yy.y"
                :title='yy.label'
            ></circle>
            <line
                :stroke="lineColor"
                :stroke-width="lineW"
                :x1='lineX1' :y1='lineY1'
                :x2='lineX2' :y2='lineY2'
            ></line>
            <circle 
                :cx="circleCenterX" 
                :cy="circleCenterY" 
                :r="circleR" 
                :stroke-width="circleBorder" 
                :stroke="circleBorderColor" 
                :fill="circleFill"
            ></circle>
        </g>
    </svg>
  </div>
</template>


<script>
import csMixins from './mixins/coordSystem'
import lineMixins from './mixins/line'
import circleMixins from './mixins/circle'
export default {
  mixins:[csMixins,lineMixins,circleMixins],
  data() {
    return {
        

    };
  },
  created() {},
  mounted() {},
  //props:{},//[]
  methods: {
      
  },
  computed: {
      
  },
  watch: {},
  filters: {},
  directives: {},
  components: {},
};
</script>