
<style scoped lang='scss'>
    table.collapse {
        border-collapse: collapse;
    }
    .cw {
        margin: 0 auto;
        min-width: 60vw;
    }
    .block {
        display: block;
    }
    #tarea {
        border:2px solid aqua;
        outline: 2px solid pink;
        min-height: 100px;
    }
    .padBottom {
        padding-bottom: 20px;
    }
    #run {
        height: 30px;
        cursor: pointer;
    }
    #clear {
        color: white;
        background: cornflowerblue;
        cursor: pointer;
    }
</style>


<template>
    <div>
        <div class='padBottom'>
            <textarea id='tarea' v-model='keyingValue' class='cw block'></textarea>
        </div>
        <div class='padBottom'>
            <button id='run' @click='handleRun' class='cw block' >RUN</button>
        </div>
        <div class='padBottom'>
            <button id='clear' @click='handleClear' class='cw block' >Clear</button>
        </div>
        <table border='1' class='collapse cw' >
            <thead>
                <tr>
                    <th>序号</th><th>内容</th><th>运行时间(ms)</th><th>加入时间</th>
                </tr>
            </thead>
            <tbody id='tbody'>
                <tr v-show='!scriptStoredList.length'>
                    <td>-1</td>
                    <td>示例脚本内容</td>
                    <td>用堆量平摊法获取的运行用时</td>
                    <td>存储于</td>
                </tr>
                <tr
                    v-for='(m,i) in scriptStoredList' 
                    :key='i'
                >
                    <td class='autoEl'> {{ i+1 }} </td>
                    <td class='autoEl'> {{ m.script }} </td>
                    <td class='autoEl'> {{ m.delta }} </td>
                    <td class='autoEl'> {{ m.startTime }} </td>
                </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
import { getFutString , deltaTimeGetter } from '@/utils/time'
export default {
    data() {
        return {
            keyingValue:'',
            scriptStoredList:[],
        };
    },
    created(){

    },
    mounted(){

    },
    //props:{},//[]
    methods:{
        handleRun(){
            let script=Function(this.keyingValue);
            let startTime=getFutString(new Date())
            let delta=deltaTimeGetter(script)
            let newCell={
                startTime,
                script:this.keyingValue,
                delta,
            }
            this.scriptStoredList.push(newCell)

        },
        handleClear(){
            this.scriptStoredList=[]
            this.keyingValue=''
        }
    },
    computed:{

    },
    watch:{

    },
    filters:{

    },
    directives:{

    },
    components:{

    },
};
</script>