
<style scoped lang='scss'>
.screenShotOutBox::v-deep {
    width: 100%;
    height: 100%;
    overflow-x:hidden;
    position:relative;
    canvas.globalShot {
        display:none;
    }
    .toolBall {
        position:fixed;
        right: 10vh;
        bottom: 10vh;
        width: 60px;
        height: 60px;
        text-align:center;
        font-size: 18px;
        line-height: 60px;
        z-index:999;
        border:1px solid #000;
        background: rgba($color: #fff, $alpha: 0.5);
        border-radius:50%;
        cursor:pointer;
        .beforeLike {
            position:absolute;
            left: calc(50% - 30px);
            top: calc(50% - 30px);
            width: 60px;
            height: 60px;
            border:1px solid #000;
            border-radius:50%;
            transition:all .5s;
        }
        .afterLike {
            position:absolute;
            left: calc(50% - 30px);
            top: calc(50% - 30px);
            width: 60px;
            height: 60px;
            border:1px solid #000;
            border-radius:50%;
            transition:all .5s;
        }
        &:hover {
            .beforeLike {
            left: calc(50% - 100px);
            top: calc(50% - 100px);
            width: 200px;
            height: 200px;
            }
            .afterLike {
            left: calc(50% - 225px);
            top: calc(50% - 225px);
            width: 450px;
            height: 450px;
            }
        }
    }
}

</style>


<template>
    <div class='screenShotOutBox' ref='screenShot'>
        <slot></slot>
        <div class="toolBall"   @click='getPNG'>
            <span>快照</span>
            <div class="beforeLike"></div>
            <div class="afterLike"></div>
        </div>
    </div>
</template>


<script>
import html2canvas from 'html2canvas';
import {downLoad} from '@/utils/utools'
export default {
    data() {
        return {
            shotCut:null
        };
    },
    created(){

    },
    mounted(){
    },
    //props:{},//[]
    methods:{
        getPNG(){
            html2canvas(this.$refs.screenShot,{
                // backgroundColor:'#e1ffbe', 
                // width:300, // 配合样式覆盖 影响内容比例尺寸 不设置时为dom布局大小
                // height:200, // 配合样式覆盖 影响内容比例尺寸
                // ignoreElements:(el)=>{ 
                //     // 可忽略head 实现取消css样式
                //     if(el.className=='ignoreItem'){
                //         return 1
                //     }
                // }
            }).then((cvs)=>{
                console.log([cvs])
                this.shotCut=cvs;
                this.$refs.shot=cvs
                cvs.className='globalShot'
                console.log([this.shotCut,this.$refs.shot])
                this.$refs.screenShot.appendChild(cvs)
                console.log([this.shotCut,this.$refs.shot])
                let aim=this.$refs.shot.toDataURL("image/png")
                console.log(aim)
                downLoad(aim)
            })
        }
    },
    computed:{

    },
    watch:{

    },
    filters:{

    },
    directives:{

    },
    components:{

    },
};
</script>