<style
    scoped
    lang='scss'
>
@import '~@/styles/jsonFormatter.scss';
</style>
<template>
    <div class="jsonProductor">
        <div class="titleBox">JSON转换器</div>
        <div class='inputBox'>
            <textarea class='tarea' v-model='tarea'></textarea>
            <button class='turning' @click='mainFunction'>转化</button>
        </div>
        <div class='strictJSON' v-html="output"></div>
        <div class="sizeHolder"></div>
        <div class='strictJSONstr' v-html="output2"></div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            output:'',
            output2:'',
            tarea:'',
        };
    },
    created(){
        
    },
    mounted(){
        
    },
    //props:{},//[]
    methods:{
        mainFunction(){
            
            if(!this.tarea){
                console.log('无输入值')
                this.output='一次转化无输入值<br /><br />'
                this.output2='二次转化无输入值<br /><br />'
                return;
            }
            try{
                let vr=(new Function('return '+this.tarea))()
                let vs=JSON.stringify(vr)
                let vss=JSON.stringify(vs)
                console.log('一次转化\n\n'+vs,'\n\n','二次转化\n\n'+vss)
                this.output='一次转化<br /><br />'+ vs
                this.output2='二次转化<br /><br />'+vss
            }catch(e){
                console.log(
                    // e.name,
                    // e.message,
                    e.stack
                )
                this.output='一次转化出错<br /><br />'
                this.output2='二次转化出错<br /><br />'
            }
        }
    },
    computed:{
        
    },
    watch:{
        
    },
    filters:{
        
    },
    directives:{
        
    },
    components:{
        
    },
};
</script>