
export class LandRuleInterface{
    constructor(options={}){
        this.name=options.name || 'land';
        this.code=options.code || ('land_'+options.name);
        this.price = options.price || 1000;
        this.housePrice = options.housePrice || 1000;
        this.houseCount = options.houseCount || 0; // 0 1 2 3 4 hotel
        this.color = options.color || '#efb12a';
        this.owner = options.owner || 'bank'
        this.onMortgage=false;
        this.tollOffset = options.tollOffset || 0
        this.type='land'
    }
    tollMod = [6,30,90,110,130,150];
    mortgageValue(){
        return this.price / 2
    }
    ownTo(name){
        this.owner=name;
    }
    addBuilding(){
        if(isNaN(this.houseCount)){
            return 'max'
        }else{
            if(this.houseCount<4){
                this.houseCount++
            }else{
                this.houseCount='hotel'
            }
            return this.houseCount
        }
    }
    toll(completeColor=false){
        if(this.owner!=='bank'&&!this.onMortgage)
        if(completeColor&&this.houseCount==0){
            return (this.price + this.tollOffset) / 100 * this.tollMod[0] * 2
        }
        if(this.houseCount=='hotel'){
            return (this.price + this.tollOffset) / 100 * this.tollMod[5]
        }else{
            return (this.price + this.tollOffset) / 100 * this.tollMod[this.houseCount]
        }
    }
}
export class StationRuleInterface{
    constructor(options={}){
        this.name = options.name;
        this.code = options.code;
        this.type='station'
        this.price=2000;
        this.owner='bank'
        this.onMortgage=false; // true
    }
    ownTo(playerName){
        this.owner=playerName
    }
    mortgageValue(){
        return this.price / 2
    }
    toll(stationCount=0){
        if(stationCount){
            return 250 * stationCount
        }else{
            return 0
        }

    }
}
