let baseCash = 2 * ( 5000 + 2000 + 1000 + 500 + 200 + 100 + 50 + 10 )
export class PlayerInterface {
    constructor(options={}){
        this.name = options.name;
        this.sign = options.sign || '#fff';
        this.cash=baseCash;
        this.stableProperty=[]; // <Array<{ pos  color }>>
        this.pos=0;
        this.stop=0
        this.status='normal' // red  bankruptcy
    }
    arrested(){ // 被逮捕
        this.stop=1;
        this.status='red';
    }
    released(){ // 被释放
        this.stop=0;
        this.status='normal'
    }
    moveAcross(step=0){
        if(this.pos+step>=40){
            this.pos+=step;
            this.pos-=40
            if(this.pos>0){
                this.passByStart()
            }
        }else{
            this.pos+=step;
        }
    }
    passByStart(){
        if(this.status!=='red'){
            this.cash+=2000
        }else{
            this.status='normal'
        }
    }
    beforeRound(){
        if(this.stop){
            this.stop=0;
            return 0
        }else{
            return 1
        }
    }
    roll(){
        return Math.floor(Math.random()*12)
    }
    getInStop(){
        this.stop=1;
    }
    getOutStop(){
        this.stop=0;
    }
    moveTo(pos,isArrested){
        if(this.pos<pos){
            this.pos=pos;
        }else{
            if(isArrested){
                this.pos=pos;
                this.status='red'
                this.stop=1;
            }else{
                this.passByStart()
            }
        }
    }
    lostMoney(money=0){
        this.cash-=money;
    }
    getMoney(money=0){
        this.cash+=money;
    }
}
export class StablePropertyInterface {
    constructor(options={}){
        this.id=options.id
        
        this.landType; // color trainStat elecWater
        this.building; // 0 1 2 3 4 hotel
        this.status='normal' // mortgage
        this.type=options.type || 'land' // land station company
    }
    addBuilding(){
        if(isNaN(this.building)){
            return 'max'
        }else{
            if(this.building<4){
                this.building++
            }else{
                this.building='hotel'
            }
            return this.building
        }
    }
    removeBuilding(){
        if(this.building){
            if(isNaN(this.building)){
                this.building=0
            }else{
                this.building--
            }
            return this.building
        }else{
            return this.building
        }
    }
}

