怪兽卡
<style scoped lang='scss'>
    $ssrColor:gold;
    $srColor:yellow;
    $rColor:aqua;
    $nColor:greenyellow;
    $snColor:silver;
    $ssnColor:#eee;
    .monsterCardWrapper {
        position: relative;
        height: 100%;
    }
    .mainArea {
        width: 300px;
        margin: 0 auto;
        border: 1px dashed aquamarine;
        .cardScreen {
            height: 400px;
            text-align: center;
        }
        .cardTitle {
            height: 60px;
            text-align: center;
        }
        .cardDescription {
            height: 60px;
            text-align: center;
        }
        .getCard {
            height: 40px;
            text-align: center;
            .startBtn {
                width: 80px;
                height: 30px;
            }
            .tenStart {
                width: 80px;
                height: 30px;
            }
        }

    }
    .maskArea {
        position: absolute;
        left: 0;
        top: 0;
        bottom: 0;
        right: 0;
        z-index: 200;
        background: rgba($color: #000000, $alpha: 0.3);
        .flexWrapper {
            display:flex;
            flex-wrap: wrap;
            justify-content: space-around;
            align-items: center;
            align-content: space-around;
            min-height: 90vh;
            .flexCell {
                width: 18vw;
                border: 2px dashed aquamarine;
                %common {
                    font-weight: 700;
                }
                &-ssr {
                    @extend %common;
                    background:$ssrColor;
                }
                &-sr {
                    @extend %common;
                    background:$srColor;
                }
                &-r {
                    @extend %common;
                    background:$rColor;
                }
                &-n {
                    @extend %common;
                    background:$nColor;
                }
                &-sn {
                    @extend %common;
                    background:$snColor;
                }
                &-ssn {
                    @extend %common;
                    background:$ssnColor;
                }
                .cardShower {
                    height: 40vh;
                }
            }
        }
    }
    .theCardGotten {
        .cardBank {
            margin: 0 auto;
            border-collapse: collapse;
            .rareness {
                %common {
                    font-weight: 700;
                }
                &-ssr {
                    @extend %common;
                    color:$ssrColor;
                }
                &-sr {
                    @extend %common;
                    color:$srColor;
                }
                &-r {
                    @extend %common;
                    color:$rColor;
                }
                &-n {
                    @extend %common;
                    color:$nColor;
                }
                &-sn {
                    @extend %common;
                    color:$snColor;
                }
                &-ssn {
                    @extend %common;
                    color:$ssnColor;
                }
            }
        }
    }
</style>


<template>
    <div class='monsterCardWrapper'>
        <div class="mainArea">
            <div class="cardScreen"> {{ '封面' }} </div>
            <div class="cardTitle"> {{ name }} </div>
            <div class="cardDescription"> {{ type }} </div>
            <div class="getCard">
                <input class='startBtn' type="button" value="抽卡"
                    @click='handleGetCard'
                />
                <input class='tenStart' type="button" value="十连"
                    @click='handleGetTenCard'
                />
            </div>
        </div>
        <div class="maskArea" v-show='justOnGetTen'>
            <div class="flexWrapper"  @click.self='closeTenMask'>
                <div 
                    v-for='(cell,i) in activeTenList' :key='i'
                    :class="'flexCell flexCell-'+handleCellType(cell.type)"
                >
                    <div class="cardShower">
                        {{cell.name}} : {{ cell.type }}
                    </div>
                </div>
            </div>
        </div>
        <div class="theCardGotten">
            <table border='1' class="cardBank">
                <thead>
                    <tr>
                        <td colspan="4">已抽{{ howManyCard }}次</td>
                    </tr>
                    <tr>
                        <td colspan="4">已获 SSR {{howManySSR}} 张 SR {{howManySR}} 张 R {{howManyR }}张 </td>
                    </tr>
                    <tr>
                        <td colspan="4">已获 N {{howManyN}} 张 SN {{howManySN}} 张 SSN {{howManySSN}} 张</td>
                    </tr>
                    <tr>
                        <th>序号</th>
                        <th>卡名</th>
                        <th>稀有度</th>
                        <th>数量</th>
                    </tr>
                </thead>
                <tbody>
                    <tr 
                        v-for="(cell,i) in sortByType" 
                        :key='i'
                        :class="'rareness-'+handleCellType(cell.type)"
                    >
                        <td> {{ i+1 }} </td>
                        <td> {{ cell.name }} </td>
                        <td> {{ cell.type }} </td>
                        <td> {{ cell.num }} </td>
                    </tr>
                </tbody>
            </table>
        </div>
    </div>
</template>


<script>
import { landTheRateNum } from '@/utils/random/basedInCellRandPow'
export default {
    data() {
        return {
            justOnGetTen:false,
            name:'',
            type:'',
            resultList:[],
            activeTenList:[],
        };
    },
    created(){},
    mounted(){},
    //props:{},//[]
    methods:{
        handleGetCard(){
            let cell=landTheRateNum();
            this.name=cell.name;
            this.type=cell.type;
            let index=this.resultList.findIndex(m=>{
                return m.name===cell.name&&m.type==cell.type;
            })
            if(this.justOnGetTen){
                this.activeTenList.push(cell)
            }
            if(index!=-1){
                if(this.resultList[index].num){
                    this.resultList[index].num++
                }else{
                    this.resultList[index].num=2
                }
                this.$set(this,'resultList',this.resultList)
            }else{
                let result=JSON.parse(JSON.stringify(cell))
                this.resultList.push(result)
            }
            console.log('----this.resultList----',this.resultList)
        },
        handleGetTenCard(){
            this.activeTenList=[]
            this.justOnGetTen=true
            for(let i=0;i<10;i++){
                this.handleGetCard()
            }
        },
        closeTenMask(){
            this.justOnGetTen=false;
            this.activeTenList=[]
        },
        handleCellType(type){
            return type.toLowerCase();
        }
    },
    computed:{
        howManyCard(){
            if(this.resultList.length){
                return this.resultList.reduce((s,p)=>{
                    return (s.num || (typeof(s)==='object'?1:s) ) + (p.num || 1)
                })
            }else{
                return 0
            }
        },
        howManySSR(){
            return this.resultList.filter(m=>m.type==='SSR').length;
        },
        howManySR(){
            return this.resultList.filter(m=>m.type==='SR').length;
        },
        howManyR(){
            return this.resultList.filter(m=>m.type==='R').length;
        },
        howManyN(){
            return this.resultList.filter(m=>m.type==='N').length;
        },
        howManySN(){
            return this.resultList.filter(m=>m.type==='SN').length;
        },
        howManySSN(){
            return this.resultList.filter(m=>m.type==='SSN').length;
        },
        sortByType(){
            let types=['SSN','SN','N','R','SR','SSR']
            return this.resultList.sort((a,b)=>{
                return -(types.indexOf(a.type) - types.indexOf(b.type))
            })
        },
    },
    watch:{},
    filters:{},
    directives:{},
    components:{},
};
</script>