<style
    scoped
    lang='scss'
>
@import '~@/styles/json2sql.scss';
</style>
<template>
    <div class="jsonProductor">
        <div class="titleBox">JSON转换SQL字串</div>
        <div class='inputBox'>
            <textarea class='tarea' v-model='tarea'></textarea>
            <button class='turning' @click='mainFunction'>转化</button>
        </div>
        <div class='strictJSON' v-html="output"></div>
        <div class="sizeHolder"></div>
    </div>
</template>
<script>
import {
    Obj2SQLstr
} from './utils'
export default {
    data() {
        return {
            output:'',
            output2:'',
            tarea:'',
        };
    },
    created(){
        
    },
    mounted(){
        
    },
    //props:{},//[]
    methods:{
        mainFunction(){
            
            if(!this.tarea){
                console.log('无输入值')
                this.output='转化无输入值<br /><br />'
                return;
            }
            try{
                let vr=(new Function('return '+this.tarea))()
                let vs=JSON.stringify(vr)
                this.output='转化结果<br /><br />'+ Obj2SQLstr(JSON.parse(vs))
            }catch(e){
                console.log(
                    // e.name,
                    // e.message,
                    e.stack
                )
                this.output='转化出错<br /><br />'
            }
        }
    },
    computed:{
        
    },
    watch:{
        
    },
    filters:{
        
    },
    directives:{
        
    },
    components:{
        
    },
};
</script>