
<style scoped lang='scss'>
    a {
        text-decoration: none;
        color:orange;
        cursor: pointer;
        word-break: break-all;
        &:hover {
            text-decoration: underline orchid;
        }
    }
    .webSiteFav {
        padding: 10px 0;
    }
    .webFavContainer {
        display: flex;
        justify-content: space-around;
        flex-wrap: wrap;
        &>li {
            width: 40%;
            
        }
    }
</style>


<template>
    <div class='webSiteFav'>
        <ol class='webFavContainer'>
            <li
                v-for='(hrefLevel,i) in hrefList' :key='i'
            ><a :href="hrefLevel.href"> {{ hrefLevel.content }} </a></li>
        </ol>
    </div>
</template>


<script>
import {hrefList} from '@/content/webCollect.js'
export default {
    data() {
        return {
            hrefList
        };
    },
};
</script>